<template>
  <div class="p2">
    <p
      style="font-size:20px;height:35px; font-weight:bold;text-align: center;"
    >增&nbsp;&nbsp;值&nbsp;&nbsp;税&nbsp;&nbsp;纳&nbsp;&nbsp;税&nbsp;&nbsp;申&nbsp;&nbsp;报&nbsp;&nbsp;表</p>
    <p style="font-weight:bold;height:40px; font-size:18px;text-align: center;">（适用于增值税小规模纳税人）</p>
    <p style="width: 70%;text-align: left;float: left;">税款所属时间：自{{com.month}}-01日至{{com.month}}-30日</p>
    <p style="width: 30%;text-align: right;float: left;">填表日期：{{com.month}}-{{new Date().getDate()}}日</p>
    <p style="width:70%; float:left;height:25px; text-align:left;">纳税人名称（公章）：{{com.firm_name}}</p>
    <p style="width:30%; float:left;height:25px; text-align:right;">金额单位：元至角分</p>
    <form class="biaoge">
      <table cellpadding="0" cellspacing="0" border="1" width="100%">
        <tr>
          <td colspan="7" style="font-size: 14px;font-weight: 700;height: 35px;">应税行为（3%征收率）扣除额计算</td>
        </tr>
        <tr>
          <td>期初余额</td>
          <td>本期发生额</td>
          <td>本期扣除额</td>
          <td>期末余额</td>
        </tr>
        <tr>
          <td>1</td>
          <td>2</td>
          <td>3（3≤1+2之和，且3≤5）</td>
          <td>4=1+2-3</td>
        </tr>
        <tr>
          <td style="background-color:#d6ebff;">
            <input name="1_1" value="0.00" />
          </td>
          <td style="background-color:#d6ebff;">
            <input name="1_2" value="0.00" />
          </td>
          <td style="background-color:#d6ebff;">
            <input name="1_3" value="0.00" />
          </td>
          <td style="background-color:#ccccff;">
            <input name="1_4" readonly value="0.00" />
          </td>
        </tr>

        <tr>
          <td colspan="7" style="font-size: 14px;font-weight: 700;height: 35px;">应税行为（3%征收率）计税销售额计算</td>
        </tr>
        <tr>
          <td>全部含税收入（3%征收率）</td>
          <td>本期扣除额</td>
          <td>含税销售额</td>
          <td>不含税销售额</td>
        </tr>
        <tr>
          <td>5</td>
          <td>6=3</td>
          <td>7=5-6</td>
          <td>8=7÷1.03</td>
        </tr>
        <tr>
          <td style="background-color:#d6ebff;">
            <input name="1_5" value="0.00" />
          </td>
          <td style="background-color:#ccccff;">
            <input name="1_6" readonly value="0.00" />
          </td>
          <td style="background-color:#ccccff;">
            <input name="1_7" readonly value="0.00" />
          </td>
          <td style="background-color:#ccccff;">
            <input name="1_8" readonly value="0.00" />
          </td>
        </tr>

        <tr>
          <td colspan="7" style="font-size: 14px;font-weight: 700;height: 35px;">应税行为（5%征收率）扣除额计算</td>
        </tr>
        <tr>
          <td>期初余额</td>
          <td>本期发生额</td>
          <td>本期扣除额</td>
          <td>期末余额</td>
        </tr>
        <tr>
          <td>9</td>
          <td>10</td>
          <td>（11≤9+10之和，且11≤13）</td>
          <td>12=9+10-11</td>
        </tr>
        <tr>
          <td style="background-color:#d6ebff;">
            <input name="1_9" value="0.00" />
          </td>
          <td style="background-color:#d6ebff;">
            <input name="1_10" value="0.00" />
          </td>
          <td style="background-color:#d6ebff;">
            <input name="1_11" value="0.00" />
          </td>
          <td style="background-color:#ccccff;">
            <input name="1_12" readonly value="0.00" />
          </td>
        </tr>

        <tr>
          <td colspan="7" style="font-size: 14px;font-weight: 700;height: 35px;">应税行为（5%征收率）计税销售额计算</td>
        </tr>
        <tr>
          <td>全部含税收入（5%征收率）</td>
          <td>本期扣除额</td>
          <td>含税销售额</td>
          <td>不含税销售额</td>
        </tr>
        <tr>
          <td>13</td>
          <td>14=11</td>
          <td>15=13-14</td>
          <td>16=15÷1.05</td>
        </tr>
        <tr>
          <td style="background-color:#d6ebff;">
            <input name="1_13" value="0.00" />
          </td>
          <td style="background-color:#ccccff;">
            <input name="1_14" readonly value="0.00" />
          </td>
          <td style="background-color:#ccccff;">
            <input name="1_15" readonly value="0.00" />
          </td>
          <td style="background-color:#ccccff;">
            <input name="1_16" readonly value="0.00" />
          </td>
        </tr>
      </table>
    </form>
    <div style="margin-top: 15px" class="uk-text-center">
      <button @click="saveForm" class="uk-button uk-button-primary">
        <i class="uk-icon-save"></i> 保存
      </button>
      <button class="uk-button uk-button-primary" @click="dataRestore()">
        <i class="uk-icon-close"></i> 删除
      </button>
      <button class="uk-button uk-button-primary" @click="useless()">
        <i class="uk-icon-file-excel-o"></i> 导出Excel
      </button>
      <button class="uk-button uk-button-primary" @click="useless()">
        <i class="uk-icon-print"></i> 打印
      </button>
    </div>
  </div>
</template>

<script>
import common from '../../mixins/common'

export default {
  mixins: [common],
  data () {
    return {
      name: 27,  //当前表格的名称编号
      setting: [],
      com: []
    }
  },
  computed: {
    formData () {
      return this.$store.state.tableData[this.name]
    }
  },
  mounted () {
    this.inputChangeEvent()
    this.inputClickEvent()
    this.getSetting()  //获取后台设置数据
  },
  methods: {
    getSetting () {
      if (this.formData) {
        this.recoverFormData()
      }
      // axios.get('getSetting').then(res => {})
      //获取预设数据
      this.com = this.$store.state.companyCurrent.gscompany
      this.initData()
    },
    initData () {
      this.$Loading.start()
      let that = this
      for (let key in this.setting) {
        J('input').each(function () {
          if (J(this).attr('name') === key) {
            J(this).val((that.setting[key] * 1).toFixed(2))
            J(this).trigger('blur')
            return false
          }
        })
      }
      this.$Loading.finish()
    },
    recoverFormData () {
      this.$Loading.start()
      let that = this
      J('input').each(function () {
        let nm = J(this).attr('name')
        that.formData.map(v => {
          if (v.name === nm) {
            J(this).val(v.value)
          }
        })
      })
      this.$Loading.finish()
    },
    fn () {
      // this.fn1('1_1', '2_1', 1)
      // this.fn1('1_2', '2_2', 1)
      // this.fn1('2_1', '2_2', 2)
      // this.fn1('1_3', '2_3', 1)
      // this.fn1('2_4', '2_3', 3)
      // this.fn1('1_4', '2_4', 1)
      // this.fn1('2_3', '2_4', 4)
      // this.fn1('1_5', '2_5', 1)
      // this.fn1('4_1', '6_1', 5)
      // this.fn1('3_2', '6_2', 1)
      // this.fn1('3_1', '3_2', 6)
      // this.fn1('3_1', '3_2', 7)
      // this.fn1('3_4', '6_4', 1)
      // this.fn1('3_5', '3_4', 8)
      // this.fn1('6_5', '6_4', 9)
      this.fn1('1_1', '1_2','1_3', 10,'1_4') // 计算第4个数据

      this.fn1('1_3', '','', 11,'1_6') // 计算第6个数据

      this.fn1('1_5', '1_6','', 4,'1_7')  // 计算第7个数据

      this.fn1('1_5', '1_6','', 2,'1_8')  // 计算第8个数据

      this.fn1('1_9', '1_10','1_11', 10,'1_12') // 计算第4个数据

      this.fn1('1_11', '','', 11,'1_14') // 计算第14个数据

      this.fn1('1_13', '1_14','', 4,'1_15')  // 计算第15个数据

      this.fn1('1_13', '1_14','', 2,'1_16')  // 计算第16个数据
      
    },
    // nameA 第一个单元格的name
    // nameB 第二个单元格的name
    // type  运算类型
    // {
    // 1:a = b
    // 2:a = b+c
    // 3:a如果小于等于b则  等于b  否则等于0
    // 4:a = b-c
    // }
    fn1 (nameA, nameB, nameC, type,nameVal) {
      let sum = 0
      let n1 = J('input[name=' + nameA + ']').val() * 1
      let n2 = 0
      if (nameB !== '') {
        n2 = J('input[name=' + nameB + ']').val() * 1
      }

      let n4 = 0
      if (nameC !== '') {
        n4 = J('input[name=' + nameC + ']').val() * 1
      }

      if (type == 1) {
        sum = parseFloat(n2).toFixed(2)
        J('input[name=' + nameA + ']').val(sum)
      } else if (type == 2) {
        sum = n1 - n2
        // J('input[name=1_7]').val(parseFloat(sum).toFixed(2))
        let _v = 1.03
        if (nameVal !== '1_8') {
          _v = 1.05
        }
        let _v1_8 = sum / _v
        J('input[name='+nameVal+']').val(parseFloat(_v1_8).toFixed(2))
      } else if (type == 3) {
        if (n1 <= n2) {
          sum = parseFloat(n1).toFixed(2)
        } else {
          sum = '0.00'
          this.$Message.error('本列需小于等于第三列数据')
        }
        J('input[name=2_4]').val(sum)
      } else if (type == 4) {
        sum = n1 - n2
        J('input[name='+nameVal+']').val(parseFloat(sum).toFixed(2))
      } else if (type == 5) {
        sum = n1 + n2
        J('input[name=3_1]').val(parseFloat(sum).toFixed(2))
      } else if (type == 6) {
        sum = n1 - n2
        J('input[name=3_3]').val(parseFloat(sum).toFixed(2))
      } else if (type == 7) {
        sum = n1 - n2
        J('input[name=6_3]').val(parseFloat(sum).toFixed(2))
      } else if (type == 8) {
        let n3 = J('input[name=3_3]').val() * 1
        sum = n3 - n2
        J('input[name=' + nameA + ']').val(parseFloat(sum).toFixed(2))
      } else if (type == 9) {
        let n3 = J('input[name=6_3]').val() * 1
        sum = n3 - n2
        J('input[name=' + nameA + ']').val(parseFloat(sum).toFixed(2))
      } else if (type === 10) {
        sum = parseFloat(n1) + parseFloat(n2) - parseFloat(n4)
        J('input[name='+nameVal+']').val(parseFloat(sum).toFixed(2))
      } else if (type === 11) {
        J('input[name='+nameVal+']').val(parseFloat(n1).toFixed(2))
      }
    }
  }
}
</script>

<style scoped>
.p2 {
  text-align: center;
  width: 1200px;
  margin: 0px auto;
}

.biaoge input {
  width: 100%;
  height: 100%;
  border: 0px;
  background-color: initial;
}
</style>



